body {
  background-color: #e0f2f1;
}

#root {
  height: 100%;
}

.dashboard-parent {
  height: 100%;
}

.sport-icon {
  width: 40px;
  margin-left: 30%;
}

#Overview_photo {
  margin-top: 1%;
  width: 200px;
  -moz-border-radius: 90px;
  -webkit-border-radius: 200px;
}

#dashboard-tabs {
  width: 400px;
  margin-left: 3%;
  background: -webkit-gradient(
    linear,
    left top, right bottom,
    color-stop(30%, rgb(255, 147, 96)),
    to(rgb(255, 146, 160))
  );
  background: -webkit-linear-gradient(
    top left,
    rgb(255, 147, 96) 30%,
    rgb(255, 146, 160)
  );
  background: -o-linear-gradient(
    top left,
    rgb(255, 147, 96) 30%,
    rgb(255, 146, 160)
  );
  background: linear-gradient(
    to bottom right,
    rgb(255, 147, 96) 30%,
    rgb(255, 146, 160)
  );
}

#event_container {
  width: 430px;
  height: 470px;
  overflow-y: auto;
  position: absolute;
}

.host_img {
  /* float: left; */
  /* margin: 0% 0% 0 3%; */
  width: 180px;
  -webkit-clip-path: polygon(0% 0%, 55% 0, 81% 50%, 57% 100%, 0% 100%);
  clip-path: polygon(0% 0%, 55% 0, 81% 50%, 57% 100%, 0% 100%);
}

.event_descriptions {
  width: 200px;
  height: auto;
  position: absolute;
  margin-left: 40%;
}

.individual_event {
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  color: black;
  border: 2px solid #dddddd;
  border-radius: 12px;
  width: 390px;
  height: 130px;
  margin-bottom: 5%;
  background-color: aliceblue;
  margin: 2%;
  text-align: left;
}

.nav-tabs > li > a {
  margin-right: 20px;
}
.nav-tabs {
  border-bottom: 1px solid #ddd;
  font-size: 14px;
}

#add-btn:hover {
  border-color: gold;
}

.newGame-btn {
  border-radius: 50px;
  width: 55px;
  height: 55px;
  border: none;
  margin-left: 350px;
  margin-top: 20px;
  padding: 0;
  background-color: #ffffff;
}

#add-btn {
  width: 55px;
  border: 2px solid #1badc0;
  border-radius: 50px;
}

button:focus {
  outline: 0;
}

#add-btn:hover {
  border-color: gold;
}

.CircularProgressbar {
  width: 70px;
  background-color: white;
}
#Overview {
  display: grid;
  grid-template-columns: 500px 500px;
  grid-template-rows: 600px;
  grid-gap: 20px;
  margin-top: 20px;
  -ms-flex-pack: center;
      justify-content: center;
}

.right-half {
  grid-column: 2;
  grid-row: 1;
  -webkit-box-shadow: 0px 9px 25px #b39ddb;
          box-shadow: 0px 9px 25px #b39ddb;
  -ms-flex-pack: center;
      justify-content: center;
}

.left-top-half {
  grid-column: 1;
  grid-row: 1;
  -webkit-box-shadow: 0px 9px 25px #b39ddb;
          box-shadow: 0px 9px 25px #b39ddb;
}
.left-container {
  display: grid;
  grid-template-columns: 450px;
  grid-template-rows: 270px 250px;
  -ms-flex-pack: center;
      justify-content: center;
  grid-gap: 10px;
  margin-top: 35px;
}
.left-top {
  grid-row: 1;
  -webkit-box-shadow: 0px 9px 25px #b39ddb;
          box-shadow: 0px 9px 25px #b39ddb;
  background: -webkit-gradient(
    linear,
    left top, right bottom,
    color-stop(30%, rgb(255, 147, 96)),
    to(rgb(255, 146, 160))
  );
  background: -webkit-linear-gradient(
    top left,
    rgb(255, 147, 96) 30%,
    rgb(255, 146, 160)
  );
  background: -o-linear-gradient(
    top left,
    rgb(255, 147, 96) 30%,
    rgb(255, 146, 160)
  );
  background: linear-gradient(
    to bottom right,
    rgb(255, 147, 96) 30%,
    rgb(255, 146, 160)
  );
}
.left-bottom {
  grid-row: 2;
  -webkit-box-shadow: 0px 9px 25px #b39ddb;
          box-shadow: 0px 9px 25px #b39ddb;
  background-color: white;
}

.inner-right {
  width: 450px;
  height: 530px;
  -webkit-box-shadow: 0px 9px 25px #b39ddb;
          box-shadow: 0px 9px 25px #b39ddb;
  padding-top: 6px;
  background-color: white;
  margin: 35px auto;
}

.host_name {
  font-size: 18px;
  margin-top: 10px;
}

#circularProgressBar-container {
  width: 100px;
  position: absolute;
  margin: 0 0 0 13%;
}

