.m-calendar {
    display: inline-block;
  }
  .m-calendar table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    table-layout: fixed;
  }
  .m-calendar td {
    padding: 8px 0;
    text-align: center;
    cursor: pointer;
    color: #dfe0e4;
    border: 1px solid #dfe0e4;
  }
  .m-calendar thead td {
    color: #1385e5;
    font-size: 11px;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 12px;
  }
  .m-calendar tbody td {
    color: #666;
  }
  .m-calendar tbody td:hover {
    background: #1385e5;
    border-color: #1385e5;
    color: #fff;
  }
  .m-calendar .current-day {
    color: #1385e5;
    font-weight: 700;
  }
  .m-calendar .next-month,
  .m-calendar .prev-month {
    color: #999;
  }
  .m-calendar .toolbar {
    line-height: 30px;
    color: #1385e5;
    text-align: center;
    margin-bottom: 13px;
  }
  .m-calendar .toolbar button {
    position: relative;
    width: 30px;
    height: 30px;
    line-height: 30px;
    color: #fff;
    border: 1px solid #1385e5;
    border-radius: 50%;
    background: #1385e5;
    font-size: 20px;
    padding: 0;
    text-align: center;
    outline: 0;
    z-index: 5;
    cursor: pointer;
  }
  .m-calendar .toolbar .prev-month {
    float: left;
  }
  .m-calendar .toolbar .next-month {
    float: right;
  }
  .m-calendar .toolbar .current-date {
    color: #1385e5;
  }
  .m-time {
    color: #fff;
    padding-top: 50px;
  }
  .m-time .showtime {
    text-align: center;
  }
  .m-time .separater {
    display: inline-block;
    font-size: 32px;
    font-weight: 700;
    color: #1385e5;
    width: 32px;
    height: 65px;
    line-height: 65px;
    text-align: center;
  }
  .m-time .time-text {
    position: relative;
    left: -10px;
    font-size: 15px;
    color: #1385e5;
    margin-top: 7px;
    margin-bottom: 10px;
  }
  .m-time .sliders {
    padding: 0 10px;
  }
  .m-time .time {
    width: 65px;
    height: 65px;
    display: inline-block;
    font-size: 38px;
    line-height: 65px;
    background-color: #1385e5;
    border-radius: 3px;
    text-align: center;
  }
  .u-slider-time {
    position: relative;
    display: inline-block;
    background-color: #dfe0e4;
    border-radius: 3px;
    height: 4px;
    width: 100%;
    cursor: pointer;
  }
  .u-slider-time .value {
    position: absolute;
    background-color: #1385e5;
    border-radius: 3px;
    top: 0;
    height: 100%;
  }
  .u-slider-time .handle {
    position: absolute;
    width: 4px;
    height: 4px;
  }
  .u-slider-time .handle:after {
    position: relative;
    display: block;
    content: "";
    top: -10px;
    left: -12px;
    width: 24px;
    height: 24px;
    background-color: #fff;
    border: 3px solid #1385e5;
    border-radius: 50%;
    cursor: pointer;
  }
  .im-btn {
    display: inline-block;
    background-color: #fff;
    border: 0;
    outline: 0;
    cursor: pointer;
    line-height: 1;
  }
  .im-btn:before {
    margin-right: 6px;
  }
  .m-input-moment {
    display: inline-block;
    width: 330px;
    padding: 12px 15px;
    border-radius: 3px;
    border: 1px solid #dfe0e4;
  }
  .m-input-moment .options {
    width: 100%;
    display: inline-block;
    margin-bottom: 4px;
  }
  .m-input-moment .options button {
    float: left;
    width: 50%;
    color: #1385e5;
    text-align: center;
    font-size: 16px;
    padding: 7px;
    border: 1px solid #1385e5;
    border-radius: 3px;
  }
  .m-input-moment .options button:first-child {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .m-input-moment .options button:last-child {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  .m-input-moment .options button.is-active {
    color: #fff;
    background-color: #1385e5;
  }
  .m-input-moment .tab {
    display: none;
    height: 310px;
  }
  .m-input-moment .tab.is-active {
    display: block;
  }
  .m-input-moment .tabs {
    margin-bottom: 11px;
  }
  .m-input-moment .btn-save {
    display: block;
    margin-top: 10px;
    width: 100%;
    background-color: #1385e5;
    padding: 12px 0;
    text-align: center;
    color: #fff;
    font-size: 16px;
    border-radius: 3px;
  }
  
  .popup{
    width:80%;
    height: 40px;
    background-color:#546e7a;
    border-radius: 15px;
    margin-top: 15px;
  }
  .popup:hover{
    border-color: #b2ebf2
    
  }
  .popup:focus{
    background-color: #bdbdbd
  }

  .modal-content{
    width: 80%;
  }

  .modal-body{
    height: 150px;
  }

